import { Button, Col, Drawer, Popover, Row, Switch, Typography } from "antd";
import { useAtom } from "jotai";
import { useContext, useState } from "react";
import { RaidHelperContext } from "../../lib/contexts/raidHelper";
import { wowBuffs } from "../../lib/groups/wow-buffs";
import { wowClassesIcons } from "../../lib/groups/wow-groups";
import { usersRaidAtom } from "../../lib/url-state/atoms";
import { DiscordIcon } from "../DiscordIcon";

interface Props {
  raidIndex: number;
}

export function ModalBuffs({ raidIndex }: Props) {
  const [visible, setVisible] = useState(false);

  const [showActiveBuffs, setShowActiveBuffs] = useState(false);

  const raidHelper = useContext(RaidHelperContext);
  const [usersRaid] = useAtom(usersRaidAtom);

  const signups = raidHelper.signups.reduce((stack, player) => {
    if (player.class !== "Absence") {
      if (Number(usersRaid.charAt(player.position)) === raidIndex) {
        stack.push(`${player.class}${player.spec}`);
      }
    }
    return stack;
  }, [] as any);

  return (
    <>
      <Button
        size="small"
        style={{ float: "right" }}
        onClick={() => setVisible(true)}
      >
        Check Missing Buffs -
      </Button>
      <Drawer
        title={"Raid buffs & debuffs"}
        placement="right"
        onClose={() => setVisible(false)}
        visible={visible}
        size="large"
      >
        <div>
          <div style={{ float: "right" }}>
            Show active buffs{" "}
            <Switch onChange={() => setShowActiveBuffs(!showActiveBuffs)} />
          </div>
          {Object.keys(wowBuffs).map((wowBuff) => {
            const categories = wowBuffs[wowBuff];

            console.log(`buff-${wowBuff.replaceAll(" ", "-")}`);
            return (
              <div key={`buff-${wowBuff.replaceAll(" ", "-")}`}>
                <Typography.Title level={4} style={{ marginTop: 10 }}>
                  {wowBuff}
                </Typography.Title>
                {Object.keys(categories).map((buff) => {
                  const buffList = categories[buff];

                  const isActive = buffList.some((classBuff: string) =>
                    signups.includes(classBuff)
                  );
                  return (
                    (isActive && showActiveBuffs) ||
                    (!isActive && (
                      <Row key={`buffline-${buff.replaceAll(" ", "-")}`}>
                        <Col span={7}>
                          {isActive ? (
                            buff
                          ) : (
                            <Popover
                              placement="right"
                              content={
                                <>
                                  {buffList.map((classBuff: string) => (
                                    <DiscordIcon
                                      code={wowClassesIcons[classBuff]}
                                    />
                                  ))}
                                </>
                              }
                            >
                              <b style={{ color: "red" }}>{buff}</b>
                            </Popover>
                          )}
                        </Col>
                        <Col span={12}>
                          {buffList.map((classBuff: string) => (
                            <>
                              {signups.includes(classBuff) && (
                                <DiscordIcon
                                  code={wowClassesIcons[classBuff]}
                                />
                              )}
                            </>
                          ))}
                        </Col>
                      </Row>
                    ))
                  );
                })}
              </div>
            );
          })}
        </div>
      </Drawer>
    </>
  );
}
