import { Image } from "antd";

interface Props {
  code: string;
  alt?: boolean;
}

export function DiscordIcon({ code, alt = false }: Props) {
  const src = code.startsWith("http")
    ? code
    : `https://cdn.discordapp.com/emojis/${code}.webp?size=96&quality=lossless`;
  return (
    <Image
      preview={false}
      style={{ width: 20, marginRight: alt ? 7 : 5, marginTop: alt ? -4 : -3 }}
      src={src}
    />
  );
}
