import { ClearOutlined, PlusCircleOutlined } from "@ant-design/icons";
import { Button, Popover, Input } from "antd";
import { useContext, useState } from "react";
import { RaidHelperContext } from "../../lib/contexts/raidHelper";
import { RaidButton2, RaidButton } from "../RaidButton";

interface Props {
  currentRaidCount: number;
  addRaid: (raidName: string) => void;
}

export function RaidAddFields({ currentRaidCount, addRaid }: Props) {
  const [raidNameToAdd, setRaidNameToAdd] = useState("");
  const raidHelper = useContext(RaidHelperContext);

  const isMaxRaidsReached = currentRaidCount >= 10;

  function addtoRaidName(toAppend: string) {
    setRaidNameToAdd(`${raidNameToAdd}: ${toAppend}`);
  }

  return (
    <div style={{ display: "flex" }}>
      <Button onClick={() => setRaidNameToAdd("")}>
        <ClearOutlined />
      </Button>
      <Popover
        title="Quick add text for raid name"
        placement="topRight"
        content={
          <div>
            {/* <Button onClick={() => setUsersRaid("")}>
      <SyncOutlined />
      Reset All Players
    </Button> */}
            <div>
              <RaidButton2 onClick={setRaidNameToAdd}>Raid 10</RaidButton2>
              <RaidButton2 onClick={setRaidNameToAdd}>
                Raid 10 héroïque
              </RaidButton2>
              <RaidButton2 onClick={setRaidNameToAdd}>Raid 25</RaidButton2>
              <RaidButton2 onClick={setRaidNameToAdd}>
                Raid 25 héroïque
              </RaidButton2>
            </div>
            <div>
              <RaidButton onClick={addtoRaidName}>Naxxramas</RaidButton>
              <RaidButton onClick={addtoRaidName}>Obsidian Sanctum</RaidButton>
              <RaidButton onClick={addtoRaidName}>Ulduar</RaidButton>
              <RaidButton onClick={addtoRaidName}>Crusader</RaidButton>
              <RaidButton onClick={addtoRaidName}>Onyxia</RaidButton>
              <RaidButton onClick={addtoRaidName}>ICC</RaidButton>
              <RaidButton onClick={addtoRaidName}>Ruby Sanctum</RaidButton>
            </div>
            <div>
              <RaidButton
                onClick={() =>
                  setRaidNameToAdd(`${raidNameToAdd} (${raidHelper?.date})`)
                }
              >
                append date
              </RaidButton>
              <RaidButton
                onClick={() =>
                  setRaidNameToAdd(`${raidNameToAdd} (Achievement oriented)`)
                }
              >
                append achievements
              </RaidButton>
            </div>
          </div>
        }
      >
        <Button
          onClick={() => {
            addRaid(raidNameToAdd);
            setRaidNameToAdd("");
          }}
          disabled={isMaxRaidsReached}
        >
          <PlusCircleOutlined />
          Add Raid
        </Button>
      </Popover>

      <Input
        placeholder={isMaxRaidsReached ? "Max raid reached" : "Raid name"}
        maxLength={60}
        value={raidNameToAdd}
        onChange={(e) => setRaidNameToAdd(e.target.value)}
      />
    </div>
  );
}
